<template lang="pug">
    .table(:class="loaderClass")
        .preloader__spinner
            .preloader__animation
        transition(name="list")
            .hint(v-if="hint") 
                |           . 
                |       . 
                |             .
                |,     ,    . 
                | .         ,          .
                | ,         "".
        .table__search
            search
        .table__item
            .table__heading 
                |<i class="fas fa-user-plus"></i> 
                span  ({{ aspirantNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(aspirantText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='aspirant'", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading 
                <i class="fas fa-user-clock"></i> 
                span  ({{ sleepNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(sleepText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='sleep'", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading 
                <i class="fas fa-user-check"></i> 
                span  ({{ currentNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(currentText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='current'", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading 
                <i class="fas fa-hands-helping"></i> 
                span  ({{ favoriteNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(favoriteText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='favorite'",  :winners="winnerNumber", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading 
                <i class="fas fa-gem"></i> 
                span V.I.P. ({{ winnerNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(winnerText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='winner'", :eva="eva", v-bind:key="eva.id")
</template>

<script>
    import eva from './eva';
    import search from './search';
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'app_table',
      data(){
          return {
            loaderClass: 'table--loading',
            evasArray: [],
            searchString: '',
            aspirantText: "     ,  , ,  , ,      . ,    ,    ,  ,       ,      ,     .",
            sleepText: "    ,     ,      .      ,      .      ,     ,   ,      .          ,     ,   ,   .",
            currentText: "    ,       ,      .      ,            ,      .    .  ,   ,     ,  ,    .</br>    ,       ,      '',   ,       ,       ''.  ,     ,       (    ''),   .    V.I.P. .",
            favoriteText: '      .     ,   ,      ,   .   ,      ,      .      ,        .      ,       .    .',
            winnerText: '      .  ,       ,    .    ,     (    "")   ,      .        ,        .'
          }
      },
      props: {
          hint: Boolean
      },
      computed: {
        ...mapState({
            evas: state => state.evas.evas
        }),
        aspirantNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'aspirant'){
                    result++;
                }
            }
            return result;
        },
        sleepNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'sleep'){
                    result++;
                }
            }
            return result;
        },
        currentNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'current'){
                    result++;
                }
            }
            return result;
        },
        favoriteNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'favorite'){
                    result++;
                }
            }
            return result;
        },
        winnerNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'winner'){
                    result++;
                }
            }
            return result;
        }
      },
      methods: {
         ...mapActions(['getAllEvasFromApi']),
        searchEvas(searchValue){
          if(searchValue == ""){
            this.evasArray = this.evas;
          } else {
            const checkEvas = this.evas.filter(
            eva => eva.name.toLowerCase().indexOf(searchValue) > -1 
            || eva.id.toLowerCase().indexOf(searchValue) > -1);
            this.evasArray = checkEvas;
          }
        },
        toggleDescWindow(text) {
            eventBus.$emit('callDescWindow', {visible: true, text: text});
        }
      },
      mounted(){
        this.$nextTick(function () {
            this.getAllEvasFromApi();
        });
        eventBus.$on('loadPage', data => {
          this.loaderClass = data.loaderClass;
          this.evasArray = this.evas;
        }); 
        eventBus.$on('search', data => {
          this.searchString = data.searchString;
          this.searchEvas(this.searchString);
        }); 
      },
      components: {
        eva,
        search
      }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .table{
        display: grid;
        width: 100%;
        grid-template-columns: repeat(5, 1fr);
        border-radius: 4px;
        transition: all .4s;
        .preloader__spinner{
            display: none;
        }
    }

    .table--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .table__item, .table__search{
            opacity:0;
            z-index:-2;
        }
    } 

    .table__search{
      grid-column: 1 / 6;
      height:auto;
      padding-bottom: 20px;
      background:#ebebeb;
    }

    .table__item{
        border:2px solid white;
        border-right: none;
        height: 90vh;
        position: relative;
        &:last-of-type{
            border-right:2px solid white;
        }
    }

    .table__content{
        height: calc(100% - 3.44rem);
        overflow-y: auto;
        margin-top: 55px;
    }

    .table__heading {
        background-color: $accentColor;
        height: 50px;
        text-align: center;
        line-height: 50px;
        font-size: $h5fontSize;
        margin-bottom: 20px;
        position: absolute;
        width: 100%;
        color: $mainColor;
        display: flex;
        justify-content: center;
        align-items: center;
        span{
            margin: 0 10px;
        }
    }
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>
<style lang="scss" src="../assets/styles/elements/hint.scss"></style>
<style lang="scss" src="../assets/styles/grid/desktop-min.scss"></style>


