<template lang="pug">
    .table.table--light(:class="loaderClass")
        .preloader__spinner
            .preloader__animation
        transition(name="list")
            .hint(v-if="hint") 
                |      ,    - .
                |   ,       .
                |       ,        .
                |   ,        "".
                |         ,    "".
        .table__search
            search
        .table__item
            .table__heading 
                <i class="fas fa-archive"></i> 
                span  ({{ progress.dark_evas.length  }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(archiveText)")
            .table__content
                past_eva(v-for="eva in categoryes.dark_evas", :eva="eva", v-bind:key="eva.id", :isHistory="isHistory")
        .table__item
            .table__heading 
                <i class="fas fa-user-clock"></i> 
                span  ({{ progress.sleepers.length }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(sleepText)")
            .table__content
                past_eva(v-for="eva in categoryes.sleepers", :eva="eva", v-bind:key="eva.id", :isHistory="isHistory")
        .table__item
            .table__heading 
                <i class="fas fa-user-check"></i> 
                span  ({{ progress.currents.length }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(currentText)")
            .table__content
                past_eva(v-for="eva in categoryes.currents", :eva="eva", v-bind:key="eva.id", :isHistory="isHistory")
        .table__item
            .table__heading 
                <i class="fas fa-hands-helping"></i> 
                span  ({{ progress.favorites.length }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(favoriteText)")
            .table__content
                past_eva(v-for="eva in categoryes.favorites", :eva="eva", v-bind:key="eva.id", :isHistory="isHistory")
        .table__item
            .table__heading 
                <i class="fas fa-gem"></i> 
                span V.I.P. ({{ progress.winners.length }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(winnerText)")
            .table__content
                past_eva(v-for="eva in categoryes.winners", :eva="eva", v-bind:key="eva.id", :isHistory="isHistory")
</template>

<script>
    import past_eva from './past-eva';
    import search from './search';
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'list',
      data(){
          return {
            loaderClass: 'table--loading',
            isHistory: false,
            searchString: '',
            categoryes:{
                dark_evas: [],
                sleepers: [],
                currents: [],
                favorites: [],
                winners: []
            },
            archiveText: "     ,        .        ''.        . ,      .   ,    ,     ,   '' .     .",
            sleepText: "    ,     ,      .      ,      .      ,     ,   ,      .          ,     ,   ,   .",
            currentText: "    ,       ,      .      ,            ,      .    .  ,   ,     ,  ,    .</br>    ,       ,      '',   ,       ,       ''.  ,     ,       (    ''),   .    V.I.P. .",
            favoriteText: '      .     ,   ,      ,   .   ,      ,      .      ,        .      ,       .    .',
            winnerText: '      .  ,       ,    .    ,     (    "")   ,      .        ,        .'
          }
      },
      props: {
          hint: Boolean
      },
      computed: {
        ...mapState({
            progress: state => state.progress
        }),
        procent(){
            var result = this.progress.all / 100;
            if(result <= 0){
                return 0;
            } else {
                return result;
            }
        },
        sleepersProcent(){
            var result = (this.progress.sleepers.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        currentsProcent(){
            var result = (this.progress.currents.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        favoriteProcent(){
            var result = (this.progress.favorites.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        winnersProcent(){
            var result = (this.progress.winners.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        darkProcent(){
            var result = (this.progress.dark_evas.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
      },
      methods: {
         ...mapActions(['getProgressFromApi']),
        searchEvas(searchValue){
          if(searchValue == ""){
            for (const key in this.categoryes) {
              this.categoryes[key] = this.progress[key]; 
            }
          } else {
            for (const key in this.categoryes) {
                window[key] = this.progress[key].filter(
                eva => eva.name.toLowerCase().indexOf(searchValue) > -1 
                || eva.id.toLowerCase().indexOf(searchValue) > -1);
              this.categoryes[key] = window[key]; 
            }
          }
        },
        toggleDescWindow(text) {
            eventBus.$emit('callDescWindow', {visible: true, text: text});
        }
      },
      mounted(){
        this.$nextTick(function () {
            this.getProgressFromApi();
        });
        eventBus.$on('search', data => {
          this.searchString = data.searchString;
          this.searchEvas(this.searchString);
        });
        eventBus.$on('loadPage', data => {
          this.loaderClass = data.loaderClass;
          for (const key in this.categoryes) {
            this.categoryes[key] = this.progress[key]; 
          }
        });  
      },
      components: {
        past_eva,
        search
      }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .table{
        display: grid;
        width: 100%;
        grid-template-columns: repeat(5, 1fr);
        border-radius: 4px;
        transition: all .4s;
        .preloader__spinner{
            display: none;
        }
        &.table--light{
            background-color: #ebebeb;
        }
    }

    .table--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .table__item, .table__search{
            opacity:0;
            z-index:-2;
        }
    } 

    .table__search{
      grid-column: 1 / 6;
      height:auto;
      padding-bottom: 20px;
      background:#ebebeb;
    }

    .table__item{
        border:2px solid white;
        border-right: none;
        height: 90vh;
        position: relative;
        &:last-of-type{
            border-right:2px solid white;
        }
    }

    .table__content{
        height: calc(100% - 3.44rem);
        overflow-y: auto;
        margin-top: 55px;
    }

    .table__heading {
        background-color: $accentColor;
        height: 50px;
        text-align: center;
        line-height: 50px;
        font-size: $h5fontSize;
        margin-bottom: 20px;
        position: absolute;
        width: 100%;
        color: $mainColor;
        display: flex;
        justify-content: center;
        align-items: center;
        span{
            margin: 0 10px;
        }
    }
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>
<style lang="scss" src="../assets/styles/elements/hint.scss"></style>


